.main {
  display: flex;
  box-sizing: border-box;
  padding: 0 32px 0 32px;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  background-color: $general-white;

  @media (prefers-color-scheme: dark) {
    background-color: $general-grey;
  }

  @media (min-width: $tablet-width) {
    overflow-x: hidden;
    padding: 0;
  }
}
