@font-face {
  font-family: "nutiro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/nunito-bold.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}
@font-face {
  font-family: "source-sans-pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/source-sans-pro-regular.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}
@font-face {
  font-family: "source-sans-pro";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/source-sans-pro-semibold.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}
.button {
  font-family: "source-sans-pro", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  display: inline-block;
  box-sizing: border-box;
  padding: 12px 30px;
  background-color: #5fd788;
  border-radius: 8px;
  color: #ffffff;
  text-decoration: none;
  transition: 0.5s;
}
.button:hover {
  color: #1f2937;
}
.button:focus, .button:active {
  color: #ffffff;
  background-color: #1f2937;
}
.button--white {
  background-color: #ffffff;
  color: #1f2937;
}
.button--white:hover {
  color: #5fd788;
}
.button--white:focus, .button--white:active {
  color: #ffffff;
  background-color: #1f2937;
}
@media (prefers-color-scheme: dark) {
  .button:focus, .button:active {
    background-color: #ffffff;
    color: #5fd788;
  }
  .button--white {
    background-color: #1f2937;
    color: #ffffff;
  }
  .button--white:hover {
    background-color: #ffffff;
    color: #1f2937;
  }
  .button--white:focus, .button--white:active {
    background-color: #ffffff;
    color: #5fd788;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.parallax-container__layer {
  transition: transform 0.5s linear;
}

@keyframes fade-out {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.html {
  scroll-behavior: smooth;
}

.body {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: #ffffff;
  -webkit-tap-highlight-color: transparent;
  scroll-behavior: smooth;
}
@media (prefers-color-scheme: dark) {
  .body {
    background-color: #1f2937;
  }
}

.main {
  display: flex;
  box-sizing: border-box;
  padding: 0 32px 0 32px;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  background-color: #ffffff;
}
@media (prefers-color-scheme: dark) {
  .main {
    background-color: #1f2937;
  }
}
@media (min-width: 768px) {
  .main {
    overflow-x: hidden;
    padding: 0;
  }
}

.headline {
  display: flex;
  padding-top: 64px;
}
@media (min-width: 768px) {
  .headline {
    padding-top: 80px;
  }
}
@media (min-width: 1150px) {
  .headline {
    max-width: 1440px;
    margin: 0 auto;
  }
}

.hero-banner {
  display: flex;
  padding-top: 50px;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .hero-banner {
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1150px) {
  .hero-banner {
    padding-top: 0;
    min-height: 800px;
    max-width: 1440px;
  }
}
@media (min-width: 768px) {
  .hero-banner--inner {
    min-height: 450px;
    flex-direction: row-reverse;
  }
}

.hero-banner__text {
  display: flex;
  padding: 20px 0;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .hero-banner__text {
    width: 40%;
    padding: 100px 0 100px 100px;
    align-items: flex-start;
  }
}
@media (min-width: 768px) {
  .hero-banner__text--inner {
    margin: auto 0;
    padding: 0 57px 0 0;
    align-items: flex-end;
  }
}
@media (min-width: 1150px) {
  .hero-banner__text--inner {
    padding-right: 65px;
  }
}

.hero-banner__header {
  font-family: "nutiro", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(2.5rem, 11vw, 4.5rem);
  line-height: clamp(2.5rem, 11vw, 4.5rem);
  letter-spacing: -0.6px;
  text-align: center;
  width: 70%;
  color: #5fd788;
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .hero-banner__header {
    font-size: clamp(3.5rem, 9vw, 5.63rem);
    line-height: clamp(3.5rem, 9vw, 5.63rem);
  }
}
@media (min-width: 768px) {
  .hero-banner__header {
    width: 130%;
    text-align: left;
  }
}
@media (min-width: 1150px) {
  .hero-banner__header {
    width: 100%;
  }
}
.hero-banner__header--inner {
  width: 80%;
  font-family: "nutiro", "Arial", sans-serif;
  font-size: clamp(1.3rem, 5.3vw, 2.5rem);
  font-weight: 600;
  line-height: clamp(1.4rem, 5.8vw, 2.7rem);
  letter-spacing: -0.6px;
  text-align: center;
}
@media (min-width: 768px) {
  .hero-banner__header--inner {
    font-size: clamp(2.5rem, 4.8vw, 3.13rem);
    line-height: clamp(2.7rem, 5.2vw, 3.38rem);
  }
}
@media (min-width: 768px) {
  .hero-banner__header--inner {
    width: 150%;
    text-align: right;
  }
}

.hero-banner__paragraph {
  font-family: "source-sans-pro", "Arial", sans-serif;
  font-style: normal;
  font-size: 0.8rem;
  line-height: 1rem;
  font-weight: 600;
  color: #9ca3af;
  text-align: center;
}
@media (min-width: 768px) {
  .hero-banner__paragraph {
    text-align: left;
  }
}
@media (min-width: 768px) {
  .hero-banner__paragraph {
    width: 80%;
  }
}
.hero-banner__paragraph--inner {
  width: 80%;
}
@media (min-width: 768px) {
  .hero-banner__paragraph--inner {
    text-align: end;
    width: 100%;
  }
}

.hero-banner__shop-button {
  min-width: 274px;
  margin-top: 20px;
}

.hero-banner__image-container {
  position: relative;
  display: flex;
  height: 48vw;
  width: 59vw;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .hero-banner__image-container {
    width: 50%;
    margin: auto -8px auto 0;
    flex-wrap: wrap;
    align-content: center;
  }
}
@media (min-width: 1150px) {
  .hero-banner__image-container {
    width: 60%;
    max-width: 912px;
    max-height: 818px;
    margin: 0;
    justify-content: center;
    flex-wrap: nowrap;
  }
}
.hero-banner__image-container a {
  position: relative;
}
.hero-banner__image-container--inner {
  height: 45vw;
  width: 43vw;
  left: 4%;
  justify-content: center;
}
@media (min-width: 768px) {
  .hero-banner__image-container--inner {
    margin-top: auto;
    margin-bottom: auto;
    padding: 0 0 0 16px;
    left: 0;
  }
}
.hero-banner__image-container::before {
  content: "";
  position: absolute;
  display: flex;
  height: 100%;
  width: 100%;
  background-image: url("../images/backgrounds/hero_background-top.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 768px) {
  .hero-banner__image-container::before {
    width: 80vw;
    height: 66vw;
    left: 10px;
    bottom: 10%;
    background-position: left bottom;
  }
}
@media (min-width: 1150px) {
  .hero-banner__image-container::before {
    max-width: 1786px;
    max-height: 1230px;
    bottom: 0;
    z-index: 0;
  }
}
.hero-banner__image-container--inner::before {
  top: 5%;
  left: -2%;
  background-image: url("../images/backgrounds/hero_background-inner.svg");
}
@media (min-width: 768px) {
  .hero-banner__image-container--inner::before {
    height: 100%;
    width: 100%;
    right: 30%;
    left: auto;
    top: 0;
    background-position: bottom right;
  }
}
@media (min-width: 1150px) {
  .hero-banner__image-container--inner::before {
    right: 40%;
    top: auto;
    bottom: 0;
    max-width: 602px;
    max-height: 659px;
  }
}

.hero-banner__image {
  position: relative;
  display: block;
  height: 30vw;
  min-height: 20px;
  width: 100%;
  object-fit: cover;
}
@media (min-width: 1150px) {
  .hero-banner__image {
    margin: auto auto 0 auto;
    max-height: 550px;
    bottom: 10%;
    z-index: 5;
  }
}
.hero-banner__image--inner {
  width: 35vw;
  height: 35vw;
}
@media (min-width: 768px) {
  .hero-banner__image--inner {
    width: 40vw;
    height: 40vw;
  }
}
@media (min-width: 1150px) {
  .hero-banner__image--inner {
    max-height: 509px;
    width: 100%;
  }
}

.hero-banner__leaf-1,
.hero-banner__leaf-2,
.hero-banner__leaf-3 {
  position: absolute;
  fill: #424b25;
}

.hero-banner__leaf-1 {
  width: 10vw;
  height: 20vw;
  left: 10%;
  top: -5%;
  transform-origin: 100% 90%;
  animation: leaf-dance 3s;
}
@media (min-width: 768px) {
  .hero-banner__leaf-1 {
    top: -10%;
    left: 5%;
  }
}
@media (min-width: 1150px) {
  .hero-banner__leaf-1 {
    width: 40%;
    height: 50%;
    max-width: 259px;
    max-height: 290px;
    top: 0;
    left: 0;
    animation: none;
  }
}

.hero-banner__leaf-2 {
  height: 8vw;
  width: 6vw;
  left: 40%;
  top: 10%;
  transform-origin: 40% 100%;
  animation: leaf-dance 2.8s;
}
@media (min-width: 768px) {
  .hero-banner__leaf-2 {
    left: 40%;
    top: 10%;
  }
}
@media (min-width: 1150px) {
  .hero-banner__leaf-2 {
    height: 40%;
    width: 40%;
    top: 0;
    left: 40%;
    max-width: 157px;
    max-height: 218px;
    z-index: 5;
    animation: none;
  }
}

.hero-banner__leaf-3 {
  height: 10vw;
  width: 9vw;
  top: 22%;
  right: 10%;
  transform-origin: 20% 100%;
  animation: leaf-dance 3s;
}
@media (min-width: 768px) {
  .hero-banner__leaf-3 {
    left: 75%;
    top: 20%;
  }
}
@media (min-width: 1150px) {
  .hero-banner__leaf-3 {
    height: 50%;
    width: 50%;
    top: 25%;
    left: 50%;
    z-index: 4;
    animation: none;
  }
}

.hero-banner__image-green-leaf-1 {
  width: 30%;
  height: 30%;
  bottom: 0;
  left: 35%;
  z-index: 6;
}

.hero-banner__image-green-leaf-2 {
  height: 45%;
  width: 35%;
  top: 25%;
  z-index: 4;
}

.hero-banner__image-green-leaf-3 {
  height: 30%;
  width: 25%;
  bottom: 50%;
  left: 65%;
  z-index: 2;
}

.hero-banner__image-pear {
  height: 90%;
  width: 90%;
  left: 15%;
  top: 0;
  z-index: 2;
}

.hero-banner__image-kiwi-1 {
  height: 50%;
  width: 40%;
  bottom: 5%;
  left: 0;
  z-index: 3;
}

.hero-banner__image-kiwi-2 {
  height: 70%;
  width: 40%;
  bottom: 5%;
  right: -2%;
  z-index: 3;
}

.hero-banner__image-green-leaf-1,
.hero-banner__image-green-leaf-2,
.hero-banner__image-green-leaf-3,
.hero-banner__image-pear,
.hero-banner__image-kiwi-1,
.hero-banner__image-kiwi-2 {
  display: none;
}
@media (min-width: 1150px) {
  .hero-banner__image-green-leaf-1,
  .hero-banner__image-green-leaf-2,
  .hero-banner__image-green-leaf-3,
  .hero-banner__image-pear,
  .hero-banner__image-kiwi-1,
  .hero-banner__image-kiwi-2 {
    position: absolute;
    display: block;
    object-fit: cover;
  }
}

@keyframes leaf-dance {
  0% {
    transform: rotate(-10deg);
  }
  75% {
    transform: rotate(6deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
.highlight-block {
  position: relative;
  display: flex;
  box-sizing: border-box;
  padding: 50px 0;
  width: 100vw;
  min-width: 0;
  max-width: 1440px;
  flex-direction: column;
  align-content: center;
  z-index: 1;
}
@media (min-width: 1150px) {
  .highlight-block {
    padding: 115px 94px 0 65px;
    flex-direction: row;
    justify-content: space-between;
  }
}
.highlight-block::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-repeat: no-repeat;
  background-position: top center;
  object-fit: cover;
  z-index: 0;
}
@media (prefers-color-scheme: dark) {
  .highlight-block::after {
    opacity: 0.3;
  }
}
@media (min-width: 768px) {
  .highlight-block::after {
    background-position: top right;
  }
}
.highlight-block--bestsellers::after {
  background-position: top center;
  background-image: image-set(url("../images/backgrounds/flower-bestsellers.webp") 1x, url("../images/backgrounds/flower-bestsellers@2x.webp") 2x, url("../images/backgrounds/flower-bestsellers.png") 1x, url("../images/backgrounds/flower-bestsellers@2x.png") 2x);
}
@media (min-width: 768px) {
  .highlight-block--bestsellers::after {
    left: -42%;
    background-image: image-set(url("../images/backgrounds/flower-bestsellers_tablet.webp") 1x, url("../images/backgrounds/flower-bestsellers_tablet@2x.webp") 2x, url("../images/backgrounds/flower-bestsellers_tablet.png") 1x, url("../images/backgrounds/flower-bestsellers_tablet@2x.png") 2x);
  }
}
@media (min-width: 1150px) {
  .highlight-block--bestsellers::after {
    top: 10%;
    background-image: image-set(url("../images/backgrounds/flower-bestsellers_desktop.webp") 1x, url("../images/backgrounds/flower-bestsellers_desktop@2x.webp") 2x, url("../images/backgrounds/flower-bestsellers_desktop.png") 1x, url("../images/backgrounds/flower-bestsellers_desktop@2x.png") 2x);
  }
}
.highlight-block--trends::after {
  background-position: center 70px;
  background-image: image-set(url("../images/backgrounds/flower-trends.webp") 1x, url("../images/backgrounds/flower-trends@2x.webp") 2x, url("../images/backgrounds/flower-trends.png") 1x, url("../images/backgrounds/flower-trends@2x.png") 2x);
  padding-top: 160px;
}
@media (min-width: 768px) {
  .highlight-block--trends::after {
    left: -40%;
    background-position: top center;
    background-image: image-set(url("../images/backgrounds/flower-trends_tablet.webp") 1x, url("../images/backgrounds/flower-trends_tablet@2x.webp") 2x, url("../images/backgrounds/flower-trends_tablet.png") 1x, url("../images/backgrounds/flower-trends_tablet@2x.png") 2x);
  }
}
@media (min-width: 1150px) {
  .highlight-block--trends::after {
    background-position: center center;
    background-image: image-set(url("../images/backgrounds/flower-trends_desktop.webp") 1x, url("../images/backgrounds/flower-trends_desktop@2x.webp") 2x, url("../images/backgrounds/flower-trends_desktop.png") 1x, url("../images/backgrounds/flower-trends_desktop@2x.png") 2x);
  }
}

.highlight-block__text {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 30px 0 100px;
  z-index: 1;
}
@media (min-width: 768px) {
  .highlight-block__text {
    padding: 120px 0 100px;
  }
}
@media (min-width: 1150px) {
  .highlight-block__text {
    margin-left: 0;
    align-items: flex-start;
  }
}

.highlight-block__shop-button {
  z-index: 1;
  min-width: 268px;
}

.highlight-block__header {
  font-family: "nutiro", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(2.25rem, 10vw, 3.5rem);
  line-height: clamp(2.25rem, 10vw, 3.5rem);
  letter-spacing: -0.6px;
  text-align: center;
  color: #5fd788;
  z-index: 1;
}
@media (min-width: 768px) {
  .highlight-block__header {
    font-size: clamp(2.5rem, 4.8vw, 3.1rem);
    line-height: clamp(2.5rem, 4.8vw, 3.1rem);
  }
}

.product-container {
  display: flex;
  flex-direction: column;
}

.product-container__product-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: center;
  z-index: 1;
}
@media (min-width: 1150px) {
  .product-container__product-list {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
}

.product-container__product-item {
  position: relative;
  width: 80vw;
  height: auto;
  box-sizing: border-box;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 0 20px;
  transition: 0.5s;
}
.product-container__product-item a {
  text-decoration: none;
  color: #1f2937;
}
.product-container__product-item:hover {
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}
.product-container__product-item:focus, .product-container__product-item:active {
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
}
.swiper--nojs .product-container__product-item:first-child {
  margin-bottom: 50px;
}
.swiper--nojs .product-container__product-item:nth-child(n+3) {
  display: none;
}
@media (prefers-color-scheme: dark) {
  .product-container__product-item:hover {
    box-shadow: 2px 2px 5px rgba(249, 250, 251, 0.2);
  }
  .product-container__product-item:focus, .product-container__product-item:active {
    box-shadow: 2px 2px 5px rgba(249, 250, 251, 0.4);
  }
}
@media (min-width: 768px) {
  .product-container__product-item {
    width: 45vw;
  }
  .swiper--nojs .product-container__product-item:first-child {
    margin-bottom: 80px;
  }
}
@media (min-width: 1150px) {
  .product-container__product-item {
    width: 50%;
    max-width: 375px;
    max-height: 390px;
  }
  .swiper--nojs .product-container__product-item:first-child {
    margin-right: 15px;
    margin-bottom: 0;
  }
}

.product-container__product-image {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 0 20px;
  object-fit: cover;
  z-index: 1;
}

.product-container__product-info {
  position: absolute;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  padding: 10px 16px;
  bottom: 0;
  justify-content: space-between;
  border-radius: 0 20px;
  background-color: rgba(255, 255, 255, 0.75);
  backdrop-filter: blur(12.5px);
  z-index: 2;
}
.product-container__product-item:hover .product-container__product-info {
  background-color: rgba(243, 244, 246, 0.7);
}
.product-container__product-item:focus .product-container__product-info, .product-container__product-item:active .product-container__product-info {
  background-color: rgba(243, 244, 246, 0.9);
}
.product-container__product-info a {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (min-width: 768px) {
  .product-container__product-info {
    padding: 13px 17px 10px;
  }
}

.product-container__product-name {
  font-family: "source-sans-pro", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 1.1rem;
  line-height: 1.3rem;
  margin: 0 -20px 0 0;
}

.product-container__product-price {
  font-family: "source-sans-pro", "Arial", sans-serif;
  font-style: normal;
  font-size: 0.8rem;
  line-height: 1rem;
  display: inline-block;
  margin: 0 5px;
  padding: 3px 6px;
  background-color: #5fd788;
  color: #ffffff;
  border-radius: 8px;
  font-weight: 600;
  line-height: 1;
  vertical-align: text-top;
}

.product-container__product-description {
  font-family: "source-sans-pro", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 0.9rem;
  color: #6b7280;
  margin: 0;
  padding-right: 20px;
}

.product-container__add-cart {
