  }
}

.cart-popup__close-btn {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  right: 5%;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  color: #374151;
  cursor: pointer;
}
.cart-popup__close-btn::after, .cart-popup__close-btn::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 1px;
  left: 3px;
  border-radius: 2px;
  background-color: currentColor;
}
.cart-popup__close-btn:hover {
  color: rgba(55, 65, 81, 0.6);
}
.cart-popup__close-btn:active, .cart-popup__close-btn:focus {
  color: rgba(55, 65, 81, 0.4);
}
.cart-popup__close-btn::after {
  transform: rotate3d(0, 0, 1, 45deg);
}
.cart-popup__close-btn::before {
  transform: rotate3d(0, 0, 1, -45deg);
}
