.nav-logo {
  @include body-small;

  display: grid;
  box-sizing: border-box;
  grid-template-columns: 1.5rem 4.12rem;
  align-items: baseline;
  text-decoration: none;
  color: $general-grey;
  font-weight: 600;
  transition: 0.5s;

  @media (prefers-color-scheme: dark) {
    color: $general-white;
  }

  &:hover {
    color: $general-green;
  }

  &:focus,
  &:active {
    color: rgba($general-green, 0.6);
  }

  @media (min-width: $tablet-width) {
    &--header {
      display: flex;
      margin-right: auto;
      flex-grow: 2;
    }

    &--footer {
      grid-area: logo;
    }
  }
}

.nav-logo__image {
  height: 1.5rem;
  width: 1.5rem;
  margin: auto 0;

  &:focus,
  &:active {
    fill: rgba($general-green, 0.6);
  }
}

.nav-logo__text {
  display: flex;
  margin: auto auto auto 12px;
}
