.highlight-block {
  position: relative;
  display: flex;
  box-sizing: border-box;
  padding: 50px 0;
  width: 100vw;
  min-width: 0;
  max-width: 1440px;
  flex-direction: column;
  align-content: center;
  z-index: 1;

  @media (min-width: $desktop-width) {
    padding: 115px 94px 0 65px;
    flex-direction: row;
    justify-content: space-between;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    background-repeat: no-repeat;
    background-position: top center;
    object-fit: cover;
    z-index: 0;

    @media (prefers-color-scheme: dark) {
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      background-position: top right;
    }
  }

  &--bestsellers::after {
    background-position: top center;
    background-image: image-set(
      url("../images/backgrounds/flower-bestsellers.webp") 1x,
      url("../images/backgrounds/flower-bestsellers@2x.webp") 2x,
      url("../images/backgrounds/flower-bestsellers.png") 1x,
      url("../images/backgrounds/flower-bestsellers@2x.png") 2x
    );

    @media (min-width: $tablet-width) {
      left: -42%;
      background-image: image-set(
        url("../images/backgrounds/flower-bestsellers_tablet.webp") 1x,
        url("../images/backgrounds/flower-bestsellers_tablet@2x.webp") 2x,
        url("../images/backgrounds/flower-bestsellers_tablet.png") 1x,
        url("../images/backgrounds/flower-bestsellers_tablet@2x.png") 2x
      );
    }

    @media (min-width: $desktop-width) {
      top: 10%;
      background-image: image-set(
        url("../images/backgrounds/flower-bestsellers_desktop.webp") 1x,
        url("../images/backgrounds/flower-bestsellers_desktop@2x.webp") 2x,
        url("../images/backgrounds/flower-bestsellers_desktop.png") 1x,
        url("../images/backgrounds/flower-bestsellers_desktop@2x.png") 2x
      );
    }
  }

  &--trends::after {
    background-position: center 70px;
    background-image: image-set(
      url("../images/backgrounds/flower-trends.webp") 1x,
      url("../images/backgrounds/flower-trends@2x.webp") 2x,
      url("../images/backgrounds/flower-trends.png") 1x,
      url("../images/backgrounds/flower-trends@2x.png") 2x
    );
    padding-top: 160px;

    @media (min-width: $tablet-width) {
      left: -40%;
      background-position: top center;
      background-image: image-set(
        url("../images/backgrounds/flower-trends_tablet.webp") 1x,
        url("../images/backgrounds/flower-trends_tablet@2x.webp") 2x,
        url("../images/backgrounds/flower-trends_tablet.png") 1x,
        url("../images/backgrounds/flower-trends_tablet@2x.png") 2x
      );
    }

    @media (min-width: $desktop-width) {
      background-position: center center;
      background-image: image-set(
        url("../images/backgrounds/flower-trends_desktop.webp") 1x,
        url("../images/backgrounds/flower-trends_desktop@2x.webp") 2x,
        url("../images/backgrounds/flower-trends_desktop.png") 1x,
        url("../images/backgrounds/flower-trends_desktop@2x.png") 2x
      );
    }
  }
}

.highlight-block__text {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 30px 0 100px;
  z-index: 1;

  @media (min-width: $tablet-width) {
    padding: 120px 0 100px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 0;
    align-items: flex-start;
  }
}

.highlight-block__shop-button {
  z-index: 1;
  min-width: 268px;
}

.highlight-block__header {
  @include big-header-semibold;

  color: $general-green;
  z-index: 1;
}
