
.header {
  position: fixed;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  padding: 12px 12px 12px 22px;
  justify-content: space-between;
  align-items: center;
  opacity: 1;
  background-color: $general-white;
  box-shadow: 1px 1px 1px $light-grey;
  transition: 0.3s linear;
  transform: translateY(0);
  z-index: 18;

  &--hidden-on-scroll {
    transform: translateY(-100%);
    opacity: 0;
  }

  @media (prefers-color-scheme: dark) {
    background-color: $general-grey;
    box-shadow: 1px 1px 1px rgba($light-grey, 0.3);
  }

  @media (min-width: $tablet-width) {
    padding: 22px 17px;
    z-index: 19;
  }

  @media (min-width: $desktop-width) {
    width: 100vw;
    padding: 22px 0;
  }
}

.header__navigation {
  position: relative;
  display: flex;
  padding-left: 15px;
  box-sizing: border-box;
  width: 100%;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    position: static;
    margin: 0 auto;
    padding: 0 32px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 55px;
    max-width: 1400px;
  }
}

.header__button {
  display: none;

  @media (max-width: $mobile-width-only) {
    position: relative;
    display: block;
    box-sizing: border-box;
    width: 25px;
    height: 25px;
    margin-right: 15px;
    padding: 20px;
    top: 0;
    right: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
    z-index: 5;
  }

  & .header__burger,
  & .header__burger::before,
  & .header__burger::after {
    content: "";
    position: absolute;
    right: 0;
    width: 25px;
    height: 2px;
    border-radius: 2px;
    background-color: $general-grey;
    transition: top 0.2s 0.2s, transform 0.2s;
  }

  @media (prefers-color-scheme: dark) {
    & .header__burger,
    & .header__burger::before,
    & .header__burger::after {
      content: "";
      background-color: $general-white;
    }
  }

  & .header__burger {
    right: 10px;
    transition: background 0.2s 0.2s;
  }

  & .header__burger::before {
    top: calc(13% - 8px);
  }

  & .header__burger::after {
    top: calc(13% + 8px);
  }

  &--nav-opened .header__burger {
    background: transparent;
  }

  &--nav-opened .header__burger::before,
  &--nav-opened .header__burger::after {
    top: 50%;
    transform-origin: 50% 50%;
    transition: top 0.2s, transform 0.2s 0.3s;
  }

  &--nav-opened .header__burger::before {
    transform: rotate3d(0, 0, 1, 45deg);
  }

  &--nav-opened .header__burger::after {
    transform: rotate3d(0, 0, 1, -45deg);
  }
}

.header__navigation-lists {
  display: none;
  position: absolute;
  flex-direction: column;
  width: calc(100% + 126px);
  top: 43px;
  margin: 0 -54px;
  justify-content: flex-end;
  background-color: $general-white;

  @media (prefers-color-scheme: dark) {
    background-color: $general-grey;
  }

  @media (max-width: $mobile-width-only) {
    &--closed {
      display: none;
      opacity: 0;
    }

    &--opened,
    &--nojs {
      display: flex;
      padding-bottom: 10px;
      opacity: 1;
    }

    &--fade-out {
      opacity: 1;
      animation-name: fade-out;
      animation-duration: 0.3s;
    }

    &--fade-in {
      opacity: 0;
      animation-name: fade-in;
      animation-duration: 0.3s;
    }
  }

  @media (min-width: $tablet-width) {
    position: static;
    display: flex;
    flex-direction: row;
    width: auto;
    margin: 0 0 auto auto;
    flex-grow: 6;
    justify-content: space-between;
  }
}

.header__links,
.header__icons {
  display: grid;
  width: 100%;
  padding: 0;
  margin: 0;
  justify-items: center;
  list-style: none;

  @include body-small;
  font-weight: 600;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    width: auto;
  }
}

.header__links {
  @media (min-width: $tablet-width) {
    display: flex;
    margin: auto;
  }
}

.header__icons {
  margin-right: 37px;

  @media (min-width: $tablet-width) {
    display: flex;
    margin-right: 0;
    margin-left: auto;
  }
}

.header__links-item,
.header__icons-item {
  margin: 10px;
  padding: 0;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.header__icons-item {
  &--cart {
    position: relative;
  }

  @media (min-width: $tablet-width) {
    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}

.header__link,
.header__icon-link {
  display: flex;
  box-sizing: border-box;
  color: $general-grey;
  text-decoration: none;
  align-items: center;
  padding: 8px;
  transition: 0.5s;
  cursor: pointer;

  @media (prefers-color-scheme: dark) {
    color: $general-white;
  }

  &:hover {
    color: $general-green;
  }

  &:focus,
  &:active {
    color: rgba($general-green, 0.6);
  }

  &--search {
    background-color: transparent;
    border: none;
  }
}

.header__icon-link {
  height: 100%;

  &--search {
    transition: 0.3s;
    z-index: 2;
  }

  &--label-opened {
    transform: translateX(300%);
  }
}

.header__icon-link-name {
  margin: auto;
  color: $general-grey;

  &--search {
    margin-left: -8px;
  }

  @media (prefers-color-scheme: dark) {
    color: $general-white;
  }

  @media (min-width: $tablet-width) {
    @include visually-hidden;
  }
}

.header__icon {
  position: relative;
  height: 1rem;
  width: 1rem;
  margin-right: 5px;
  fill: currentColor;

  @media (min-width: $tablet-width) {
    margin-right: 0;
  }
}

.header__icon-quantity-container {
  position: relative;
  display: flex;
  box-sizing: border-box;
  height: 0.68rem;
  min-width: 0.68rem;
  padding: 0.15rem 0.2rem 0.2rem;
  border-radius: 0.5rem;
  top: 0.5rem;
  left: -0.5rem;
  background-color: $general-green;
  z-index: 1;

  @media (max-width: $mobile-width-only) {
    left: -0.75rem;
  }
}

.header__icon-quantity-text {
  margin: auto;
  font-size: 0.5rem;
  line-height: 0;
  font-weight: 300;
  color: $general-white;
}

.header__search-form {
  position: relative;
  display: flex;
  height: 100%;
  flex-direction: row-reverse;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.header__search-label {
  position: absolute;
  transform: scale(0, 1);
  opacity: 0;
  transition: opacity 0.5s linear;
  z-index: 1;

  &--opened {
    opacity: 1;
    transform: scale(1) translateX(20%);
  }

  @media (min-width: $tablet-width) {
    &--opened {
      transform: scale(1) translateX(-100%);
      transform-origin: left;
    }
  }
}

.header__search-input {
  display: flex;
  margin-right: -40px;
  padding: 10px 50px 10px 10px;
  border-radius: 30px;
  background-color: $light-grey;
  color: $general-grey;
  border: none;
  cursor: auto;

  &:focus-visible {
    border: none;
    outline: none;
    box-shadow: inset 0 0 1px 1px $general-green;
  }

  &::placeholder {
    @include body-small;

    color: $light-font;
  }

  @media (prefers-color-scheme: dark) {
    color: $general-white;
    background-color: $general-grey;
    box-shadow: inset 0 0 1px 1px $light-grey;
  }
}
