.blog {
  display: flex;
  max-width: 1440px;
  flex-direction: column;
  padding-bottom: 45px;
  z-index: 1;

  @media (min-width: $tablet-width) {
    padding-bottom: 70px;
  }
}

.blog__header {
  @include huge-header-semibold;

  margin: 22px 0 72px;
  text-transform: uppercase;
  color: $general-green;

  @media (min-width: $desktop-width) {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.blog__blogposts {
  display: grid;
  margin: 0;
  justify-content: center;
  gap: 40px;
  list-style: none;

  @media (min-width: $tablet-width) {
    gap: 70px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 140px;
    padding-top: 26px;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 24px;
    justify-items: center;
  }
}

.blog__blogpost-teaser {
  display: flex;
  flex-direction: column;
  width: min-content;
  height: min-content;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: 0.5s ease-in;

  &:hover {
    background-color: $light-grey;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  }

  &:focus,
  &:active {
    background-color: $super-light-grey;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
  }

  @media (prefers-color-scheme: dark) {
    background-color: $additional-grey;

    &:hover {
      background-color: rgba($additional-grey, 0.4);
      box-shadow: 2px 2px 5px rgba($ultra-light-grey, 0.1);
    }

    &:focus,
    &:active {
      background-color: rgba($additional-grey, 0.7);
      box-shadow: 2px 2px 5px rgba($ultra-light-grey, 0.2);
    }
  }
}

.blog__blogpost-link {
  text-decoration: none;
}

.blog__blogpost-text {
  padding: 0 12px 10px;

  @media (min-width: $tablet-width) {
    padding: 16px 24px 38px;
    min-height: 265px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;
    padding-bottom: 25px;
    min-height: fit-content;
  }
}

.blog__blogpost-image {
  width: 55vw;
  height: 50vw;
  border-radius: 8px 8px 0 0;
  object-fit: cover;

  @media (min-width: $tablet-width) {
    width: 45vw;
    height: 41vw;
  }

  @media (min-width: $desktop-width) {
    max-width: 371px;
    max-height: 204px;
  }
}

.blog__blogpost-header {
  @include body-huge-bold;

  position: relative;
  color: $general-green;
  margin-top: 19px;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    bottom: -8px;
    background-color: $light-grey;

    @media (prefers-color-scheme: dark) {
      background-color: rgba($light-grey, 0.2);
    }
  }

  @media (min-width: $tablet-width) {
    &::after {
      height: 2px;
      bottom: -45px;
    }
  }

  @media (min-width: $desktop-width) {
    &::after {
      bottom: -8px;
    }
  }
}

.blog__blogpost-description {
  @include body-small;

  font-weight: 600;
  color: $light-font;

  @media (prefers-color-scheme: dark) {
    color: $light-grey;
  }

  @media (min-width: $tablet-width) {
    padding-top: 60px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 14px;
  }
}
