
@font-face {
  font-family: "nutiro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/nunito-bold.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}

@font-face {
  font-family: "source-sans-pro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/source-sans-pro-regular.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}

@font-face {
  font-family: "source-sans-pro";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/source-sans-pro-semibold.woff2") format("woff2");
  font-display: swap;
  unicode-range: U+000-5FF;
}

$basic-font-family: "source-sans-pro", "Arial", sans-serif;
$highlight-font-family: "nutiro", "Arial", sans-serif;
