.product-container {
  display: flex;
  flex-direction: column;
}

.product-container__product-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
  justify-content: center;
  z-index: 1;

  @media (min-width: $desktop-width) {
    flex-wrap: nowrap;
    justify-content: space-between;
  }
}

.product-container__product-item {
  position: relative;
  width: 80vw;
  height: auto;
  box-sizing: border-box;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 0 20px;
  transition: 0.5s;

  a {
    text-decoration: none;
    color: $general-grey;
  }

  &:hover {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  }

  &:focus,
  &:active {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
  }

  .swiper--nojs &:first-child {
    margin-bottom: 50px;
  }

  .swiper--nojs &:nth-child(n+3) {
    display: none;
  }

  @media (prefers-color-scheme: dark) {
    &:hover {
      box-shadow: 2px 2px 5px rgba($ultra-light-grey, 0.2);
    }

    &:focus,
    &:active {
      box-shadow: 2px 2px 5px rgba($ultra-light-grey, 0.4);
    }
  }

  @media (min-width: $tablet-width) {
    width: 45vw;

    .swiper--nojs &:first-child {
      margin-bottom: 80px;
    }
  }

  @media (min-width: $desktop-width) {
    width: 50%;
    max-width: 375px;
    max-height: 390px;

    .swiper--nojs &:first-child {
      margin-right: 15px;
      margin-bottom: 0;
    }
  }
}

.product-container__product-image {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 0 20px;
  object-fit: cover;
  z-index: 1;
}

.product-container__product-info {
  position: absolute;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  padding: 10px 16px;
  bottom: 0;
  justify-content: space-between;
  border-radius: 0 20px;
  background-color: rgba($general-white, 0.75);
  backdrop-filter: blur(12.5px);
  z-index: 2;

  .product-container__product-item:hover & {
    background-color: rgba($light-grey, 0.7);
  }

  .product-container__product-item:focus &,
  .product-container__product-item:active & {
    background-color: rgba($light-grey, 0.9);
  }

  a {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  @media (min-width: $tablet-width) {
    padding: 13px 17px 10px;
  }
}

.product-container__product-name {
  @include body-big-bold;

  margin: 0 -20px 0 0;
}

.product-container__product-price {
  @include body-small;

  display: inline-block;
  margin: 0 5px;
  padding: 3px 6px;
  background-color: $general-green;
  color: $general-white;
  border-radius: 8px;
  font-weight: 600;
  line-height: 1;
  vertical-align: text-top;
}

.product-container__product-description {
  @include body-tiny-medium;

  color: $super-light-font;
  margin: 0;
  padding-right: 20px;
}

.product-container__add-cart {
  display: block;
  box-sizing: border-box;
  margin-top: auto;
  background-color: transparent;
  color: $general-grey;
  border-style: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;

  &:hover {
    color: $general-green;
  }

  &:focus,
  &:active {
    color: rgba($general-green, 0.6);
  }
}

.product-container__add-cart-icon {
  position: relative;
  width: 2.75rem;
  height: 2.75rem;
  fill: currentColor;
  z-index: -1;
}

.product-container__pagination {
  .swiper--nojs & {
    display: none;
  }
}
