.hero-banner {
  display: flex;
  padding-top: 50px;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: space-evenly;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;
    min-height: 800px;
    max-width: 1440px;
  }

  &--inner {
    @media (min-width: $tablet-width) {
      min-height: 450px;
      flex-direction: row-reverse;
    }
  }
}

.hero-banner__text {
  display: flex;
  padding: 20px 0;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    width: 40%;
    padding: 100px 0 100px 100px;
    align-items: flex-start;
  }

  &--inner {
    @media (min-width: $tablet-width) {
      margin: auto 0;
      padding: 0 57px 0 0;
      align-items: flex-end;
    }

    @media (min-width: $desktop-width) {
      padding-right: 65px;
    }
  }
}

.hero-banner__header {
  @include large-header-semibold;

  width: 70%;
  color: $general-green;
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    width: 130%;
    text-align: left;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
  }

  &--inner {
    width: 80%;

    @include small-header-semibold;

    @media (min-width: $tablet-width) {
      width: 150%;
      text-align: right;
    }
  }
}

.hero-banner__paragraph {
  @include body-small;

  font-weight: 600;
  color: $ultra-light-font;
  text-align: center;

  @media (min-width: $tablet-width) {
    text-align: left;
  }

  @media (min-width: $tablet-width) {
    width: 80%;
  }

  &--inner {
    width: 80%;

    @media (min-width: $tablet-width) {
      text-align: end;
      width: 100%;
    }
  }
}

.hero-banner__shop-button {
  min-width: 274px;
  margin-top: 20px;
}

.hero-banner__image-container {
  position: relative;
  display: flex;
  height: 48vw;
  width: 59vw;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;

  @media (min-width: $tablet-width) {
    width: 50%;
    margin: auto -8px auto 0;
    flex-wrap: wrap;
    align-content: center;
  }

  @media (min-width: $desktop-width) {
    width: 60%;
    max-width: 912px;
    max-height: 818px;
    margin: 0;
    justify-content: center;
    flex-wrap: nowrap;
  }

  a {
    position: relative;
  }

  &--inner {
    height: 45vw;
    width: 43vw;
    left: 4%;
    justify-content: center;

    @media (min-width: $tablet-width) {
      margin-top: auto;
      margin-bottom: auto;
      padding: 0 0 0 16px;
      left: 0;
    }
  }

  &::before {
    content: "";
    position: absolute;
    display: flex;
    height: 100%;
    width: 100%;
    background-image: url("../images/backgrounds/hero_background-top.svg");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;

    @media (min-width: $tablet-width) {
      width: 80vw;
      height: 66vw;
      left: 10px;
      bottom: 10%;
      background-position: left bottom;
    }

    @media (min-width: $desktop-width) {
      max-width: 1786px;
      max-height: 1230px;
      bottom: 0;
      z-index: 0;
    }
  }

  &--inner::before {
    top: 5%;
    left: -2%;
    background-image: url("../images/backgrounds/hero_background-inner.svg");

    @media (min-width: $tablet-width) {
      height: 100%;
      width: 100%;
      right: 30%;
      left: auto;
      top: 0;
      background-position: bottom right;
    }

    @media (min-width: $desktop-width) {
      right: 40%;
      top: auto;
      bottom: 0;
      max-width: 602px;
      max-height: 659px;
    }
  }
}

.hero-banner__image {
  position: relative;
  display: block;
  height: 30vw;
  min-height: 20px;
  width: 100%;
  object-fit: cover;

  @media (min-width: $desktop-width) {
    margin: auto auto 0 auto;
    max-height: 550px;
    bottom: 10%;
    z-index: 5;
  }

  &--inner {
    width: 35vw;
    height: 35vw;

    @media (min-width: $tablet-width) {
      width: 40vw;
      height: 40vw;
    }

    @media (min-width: $desktop-width) {
      max-height: 509px;
      width: 100%;
    }
  }
}

.hero-banner__leaf-1,
.hero-banner__leaf-2,
.hero-banner__leaf-3 {
  position: absolute;
  fill: $general-icon;
}

.hero-banner__leaf-1 {
  width: 10vw;
  height: 20vw;
  left: 10%;
  top: -5%;
  transform-origin: 100% 90%;
  animation: leaf-dance 3s;


  @media (min-width: $tablet-width) {
    top: -10%;
    left: 5%;
  }

  @media (min-width: $desktop-width) {
    width: 40%;
    height: 50%;
    max-width: 259px;
    max-height: 290px;
    top: 0;
    left: 0;
    animation: none;
  }
}

.hero-banner__leaf-2 {
  height: 8vw;
  width: 6vw;
  left: 40%;
  top: 10%;
  transform-origin: 40% 100%;
  animation: leaf-dance 2.8s;

  @media (min-width: $tablet-width) {
    left: 40%;
    top: 10%;
  }

  @media (min-width: $desktop-width) {
    height: 40%;
    width: 40%;
    top: 0;
    left: 40%;
    max-width: 157px;
    max-height: 218px;
    z-index: 5;
    animation: none;
  }
}

.hero-banner__leaf-3 {
  height: 10vw;
  width: 9vw;
  top: 22%;
  right: 10%;
  transform-origin: 20% 100%;
  animation: leaf-dance 3s;

  @media (min-width: $tablet-width) {
    left: 75%;
    top: 20%;
  }

  @media (min-width: $desktop-width) {
    height: 50%;
    width: 50%;
    top: 25%;
    left: 50%;
    z-index: 4;
    animation: none;
  }
}

.hero-banner__image-green-leaf-1 {
  width: 30%;
  height: 30%;
  bottom: 0;
  left: 35%;
  z-index: 6;
}

.hero-banner__image-green-leaf-2 {
  height: 45%;
  width: 35%;
  top: 25%;
  z-index: 4;
}

.hero-banner__image-green-leaf-3 {
  height: 30%;
  width: 25%;
  bottom: 50%;
  left: 65%;
  z-index: 2;
}

.hero-banner__image-pear {
  height: 90%;
  width: 90%;
  left: 15%;
  top: 0;
  z-index: 2;
}

.hero-banner__image-kiwi-1 {
  height: 50%;
  width: 40%;
  bottom: 5%;
  left: 0;
  z-index: 3;
}

.hero-banner__image-kiwi-2 {
  height: 70%;
  width: 40%;
  bottom: 5%;
  right: -2%;
  z-index: 3;
}

.hero-banner__image-green-leaf-1,
.hero-banner__image-green-leaf-2,
.hero-banner__image-green-leaf-3,
.hero-banner__image-pear,
.hero-banner__image-kiwi-1,
.hero-banner__image-kiwi-2 {
  display: none;

  @media (min-width: $desktop-width) {
    position: absolute;
    display: block;
    object-fit: cover;
  }
}

@keyframes leaf-dance {
  0% {
    transform: rotate(-10deg);
  }

  75% {
    transform: rotate(6deg);
  }

  100% {
    transform: rotate(0deg);
  }
}
