.footer {
  display: flex;
  background-color: $light-grey;

  @media (prefers-color-scheme: dark) {
    background-color: $additional-grey;
  }
}

.footer__content {
  display: flex;
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 28px 32px 38px;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    grid-template-areas:
      "logo links ."
      "copyright copyright copyright";
    padding: 35px 85px;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.footer__links {
  display: grid;
  padding: 50px 0;
  gap: 33px;

  @media (min-width: $tablet-width) {
    padding-top: 0;
    grid-area: links;
    gap: 36px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.footer__links-group {
  display: grid;
  margin: 0;
  padding: 0;
  justify-content: center;
  list-style: none;

  @media (min-width: $desktop-width) {
    max-width: 15vw;
  }
}

.footer__link-item {
  max-height: fit-content;
  text-align: center;
}

.footer__link {
  @include body-small;

  text-decoration: none;
  color: $ultra-light-font;
  transition: 0.5s;

  &:hover {
    color: rgba($general-green, 0.7);
  }

  &:focus,
  &:active {
    color: rgba($general-green, 0.5);
  }

  &--header {
    color: $general-green;
    font-weight: 600;

    &:hover {
      color: rgba($ultra-light-font, 0.7);
    }

    &:focus,
    &:active {
      color: rgba($ultra-light-font, 0.5);
    }
  }
}

.footer__copyright {
  @include body-small;

  color: $super-light-font;
  text-align: center;

  @media (min-width: $tablet-width) {
    min-width: 100%;
    grid-area: copyright;
  }
}
