.swiper {
  width: 80vw;
  padding: 5px;

  @media (min-width: $tablet-width) {
    width: 45vw;
    padding-bottom: 100px;
  }

  @media (min-width: $desktop-width) {
    width: 59%;
    margin-right: 0;
    padding-bottom: 50px;
  }
}

.swiper-pagination {
  position: static;
  display: flex;
  max-width: fit-content;
  margin: auto;
  padding: 22px 0;

  @media (min-width: $tablet-width) {
    padding: 41px 0;
  }

  @media (min-width: $desktop-width) {
    margin: auto;
    padding: 47px 0;
  }
}

.swiper-pagination-bullets {
  margin: auto;
}

.swiper-pagination-bullet {
  display: block;
  box-sizing: border-box;
  width: 8px;
  height: 8px;
  margin: 3px;
  padding: 0;
  opacity: 1;
  background-color: $ultra-light-font;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s;

  &-active {
    background-color: $general-green;
  }
}
