@mixin huge-header-semibold {
  font-family: $highlight-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(3rem, 13.2vw, 4rem);
  line-height: clamp(3rem, 13.2vw, 4rem);
  letter-spacing: -0.6px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: clamp(3.12rem, 36vw, 4rem);
    line-height: clamp(3.12rem, 36vw, 4rem);
  }
}

@mixin large-header-semibold {
  font-family: $highlight-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(2.5rem, 11vw, 4.5rem);
  line-height: clamp(2.5rem, 11vw, 4.5rem);
  letter-spacing: -0.6px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: clamp(3.5rem, 9vw, 5.63rem);
    line-height: clamp(3.5rem, 9vw, 5.63rem);
  }
}

@mixin big-header-semibold {
  font-family: $highlight-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(2.25rem, 10vw, 3.5rem);
  line-height: clamp(2.25rem, 10vw, 3.5rem);
  letter-spacing: -0.6px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: clamp(2.5rem, 4.8vw, 3.1rem);
    line-height: clamp(2.5rem, 4.8vw, 3.1rem);
  }
}

@mixin small-header-semibold {
  font-family: $highlight-font-family;
  font-size: clamp(1.3rem, 5.3vw, 2.5rem);
  font-weight: 600;
  line-height: clamp(1.4rem, 5.8vw, 2.7rem);
  letter-spacing: -0.6px;
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: clamp(2.5rem, 4.8vw, 3.13rem);
    line-height: clamp(2.7rem, 5.2vw, 3.38rem);
  }
}

@mixin accent-header {
  font-family: $highlight-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: clamp(2rem, 8.5vw, 2.38rem);
  line-height: clamp(2rem, 8.52vw, 2.8rem);
  text-align: center;

  @media (min-width: $tablet-width) {
    font-size: clamp(2.38rem, 4.56vw, 3.12rem);
    line-height: clamp(2.81rem, 5.4vw, 3.75rem);
  }
}

@mixin body-huge-bold {
  font-family: $basic-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: 1.3rem;
  line-height: 1.5rem;
}

@mixin body-big-bold {
  font-family: $basic-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: 1.1rem;
  line-height: 1.3rem;
}

@mixin body {
  font-family: $basic-font-family;
  font-style: normal;
  font-size: 0.9rem;
  line-height: 1.3rem;
  text-align: center;
}

@mixin body-small {
  font-family: $basic-font-family;
  font-style: normal;
  font-size: 0.8rem;
  line-height: 1rem;
}

@mixin body-tiny-medium {
  font-family: $basic-font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 0.9rem;
}

@mixin extra-small-bold {
  font-family: $basic-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  letter-spacing: 0.01px;
  text-transform: uppercase;
}

@mixin btn-font {
  font-family: $basic-font-family;
  font-style: normal;
  font-weight: 600;
  font-size: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
}
