.button {
  @include btn-font;

  display: inline-block;
  box-sizing: border-box;
  padding: 12px 30px;
  background-color: $general-green;
  border-radius: 8px;
  color: $general-white;
  text-decoration: none;
  transition: 0.5s;

  &:hover {
    color: $general-grey;
  }

  &:focus,
  &:active {
    color: $general-white;
    background-color: $general-grey;
  }

  &--white {
    background-color: $general-white;
    color: $general-grey;

    &:hover {
      color: $general-green;
    }

    &:focus,
    &:active {
      color: $general-white;
      background-color: $general-grey;
    }
  }

  @media (prefers-color-scheme: dark) {
    &:focus,
    &:active {
      background-color: $general-white;
      color: $general-green;
    }

    &--white {
      background-color: $general-grey;
      color: $general-white;

      &:hover {
        background-color: $general-white;
        color: $general-grey;
      }

      &:focus,
      &:active {
        background-color: $general-white;
        color: $general-green;
      }
    }
  }
}
