.explore {
  display: flex;
  padding: 40px 0;
  width: 100vw;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    padding: 80px 0;
  }

  @media (min-width: $desktop-width) {
    max-width: 1440px;
    padding: 100px 0;
  }
}

.explore__product-groups {
  display: grid;
  width: 33vw;
  min-height: 122px;
  min-width: 122px;
  margin: auto;
  padding: 0;
  grid-template-columns: 1fr;
  gap: 20px;
  list-style: none;

  @media (min-width: $tablet-width) {
    gap: 46px;
  }

  @media (min-width: $desktop-width) {
    width: 80%;
    grid-template-columns: repeat(4, 1fr);
    gap: 50px 0;
  }
}

.explore__product-group {
  position: relative;
  display: inline-block;
  height: fit-content;
  margin: 0;
  padding: 0;

  .explore__product-groups &:nth-last-child(4n) {
    z-index: 3;
  }

  .explore__product-groups &:nth-last-child(4n - 1) {
    z-index: 2;
  }

  .explore__product-groups &:nth-last-child(4n - 2) {
    z-index: 1;
  }

  .explore__product-groups &:nth-last-child(4n - 3) {
    z-index: 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: -5px;
  }
}

.explore__product-group-link {
  display: flex;
  height: auto;
  box-sizing: border-box;
  flex-direction: column;
  padding: 25% 2% 18%;
  align-self: center;
  align-items: center;
  text-decoration: none;
  background-color: $ultra-light-grey;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 0 20px;
  transition: 0.5s;

  @media (prefers-color-scheme: dark) {
    background-color: $additional-grey;
  }

  @media (min-width: $tablet-width) {
    height: 100%;
    padding-bottom: 25%;
  }

  &:hover {
    background-color: $general-green;

    @media (prefers-color-scheme: dark) {
      background-color: $general-green;
    }
  }

  &:focus,
  &:active {
    background-color: $additional-grey;

    @media (prefers-color-scheme: dark) {
      background-color: $ultra-light-grey;
    }
  }
}

.explore__product-group-icon-container {
  position: relative;
  display: flex;
  width: 35%;
  height: fit-content;
}

.explore__product-group-background {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  bottom: 0;
  right: 0;
  fill: $general-green;

  .explore__product-group-link:hover & {
    fill: $general-white;
  }

  .explore__product-group-link:focus &,
  .explore__product-group-link:active & {
    fill: $general-green;
  }

  @media (min-width: $desktop-width) {
    max-width: 85px;
    max-height: 90px;
  }
}

.explore__product-group-icon {
  height: 12vw;
  width: 11vw;
  min-height: 39px;
  min-width: 37px;
  fill: $general-icon;
  z-index: 2;

  .explore__product-group-link:focus &,
  .explore__product-group-link:active & {
    fill: $general-white;
  }

  @media (prefers-color-scheme: dark) {
    fill: $general-white;

    .explore__product-group-link:hover & {
      fill: $general-icon;
    }

    .explore__product-group-link:focus &,
    .explore__product-group-link:active & {
      fill: $general-icon;
    }
  }

  @media (min-width: $desktop-width) {
    max-width: 110px;
    max-height: 110px;
  }
}

.explore__product-group-name {
  @include body;

  margin: 0;
  margin-top: 5px;
  color: $general-green;
  font-weight: 600;


  .explore__product-group-link:hover > & {
    color: $general-white;
  }

  .explore__product-group-link:focus > &,
  .explore__product-group-link:active > & {
    color: $general-green;
  }

  @media (min-width: $tablet-width) {
    margin-top: 15px;
  }
}
