.banner-block {
  display: flex;
  flex-direction: column;
  margin: 0 -32px 0 -32px;
  padding: 113px 30px 140px;
  color: $general-white;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  &--soaps {
    background-image: image-set(
      url("../images/banners/soap.webp") 1x,
      url("../images/banners/soap@2x.webp") 2x,
      url("../images/banners/soap.jpg") 1x,
      url("../images/banners/soap@2x.jpg") 2x
    );

    @media (min-width: $tablet-width) {
      padding: 0;
      flex-direction: row;
      justify-content: space-between;
      background: $general-green;
    }
  }

  @media (min-width: $desktop-width) {
    max-width: 1440px;
  }
}

.banner-block__image {
  display: none;
  object-fit: cover;

  &--soaps {
    @media (min-width: $tablet-width) {
      display: flex;
      width: 36%;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: cover;
      background-image: image-set(
        url("../images/banners/soap_tablet.webp") 1x,
        url("../images/banners/soap_tablet@2x.webp") 2x,
        url("../images/banners/soap_tablet.jpg") 1x,
        url("../images/banners/soap_tablet@2x.jpg") 2x
      );
    }

    @media (min-width: $desktop-width) {
      width: 45%;
      background-image: image-set(
        url("../images/banners/soap_desktop.webp") 1x,
        url("../images/banners/soap_desktop@2x.webp") 2x,
        url("../images/banners/soap_desktop.jpg") 1x,
        url("../images/banners/soap_desktop@2x.jpg") 2x
      );
    }
  }
}

.banner-block__text {
  display: flex;
  box-sizing: border-box;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    position: relative;
    width: 65%;
    box-sizing: border-box;
    padding: 80px 115px 100px 67px;
    align-items: flex-start;
    background-color: $general-green;

    &::before {
      content: "";
      position: absolute;
      min-width: 100%;
      min-height: 100%;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      background-image: image-set(
        url("../images/backgrounds/texture_tablet.webp") 1x,
        url("../images/backgrounds/texture_tablet@2x.webp") 2x,
        url("../images/backgrounds/texture_tablet.png") 1x,
        url("../images/backgrounds/texture_tablet@2x.png") 2x
      );
      z-index: 1;
    }
  }

  @media (min-width: $desktop-width) {
    width: 55%;
    padding: 72px 140px 100px 100px;
  }
}

.banner-block__header {
  @include accent-header;

  padding-bottom: 15px;

  @media (min-width: $tablet-width) {
    text-align: left;
    z-index: 2;

    @media (prefers-color-scheme: dark) {
      color: $general-grey;
    }
  }
}

.banner-block__button {
  width: auto;
  margin: 0 62px;

  @media (min-width: $tablet-width) {
    min-width: 187px;
    margin: 0;
    z-index: 2;
  }
}
