.cart-popup {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: 0;
  padding: 0;
  background-color: rgba($light-grey, 0.2);
  display: flex;
  z-index: 20;
  backdrop-filter: blur(4px);
  overflow-y: auto;

  &--fade-out {
    opacity: 1;
    animation-name: fade-out;
    animation-duration: 0.5s;
  }

  &--fade-in {
    opacity: 0;
    animation-name: fade-in;
    animation-duration: 0.5s;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
    backdrop-filter: blur(0);
  }
}

.cart-popup__wrapper {
  @include body-small;

  position: relative;
  display: flex;
  margin: auto;
  padding: 15px;
  width: 80vw;
  max-width: 1300px;
  box-sizing: border-box;
  flex-wrap: wrap;
  color: $general-grey;
  background-color: $light-grey;
  border-radius: 0 20px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);

  @media (min-width: $desktop-width) {
    margin-top: 0;
    margin-right: 0;
    width: 30vw;
    max-width: 740px;

    &--empty {
      width: 20vw;
    }
  }
}

.cart-popup__close-btn {
  position: absolute;
  display: inline-block;
  width: 20px;
  height: 20px;
  right: 5%;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  color: $additional-grey;
  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    left: 3px;
    border-radius: 2px;
    background-color: currentColor;
  }

  &:hover {
    color: rgba($additional-grey, 0.6);
  }

  &:active,
  &:focus {
    color: rgba($additional-grey, 0.4);
  }

  &::after {
    transform: rotate3d(0, 0, 1, 45deg);
  }

  &::before {
    transform: rotate3d(0, 0, 1, -45deg);
  }

  @media (min-width: $desktop-width) {
    display: none;
  }
}

.cart-popup__header {
  @include body-big-bold;

  margin: 25px auto 30px;

  &--full {
    margin: 20px auto 20px 10px;
  }
}

.cart-popup__product-container {
  display: flex;
  width: 100%;
  padding: 0;
  gap: 20px;
  flex-direction: column;
  list-style: none;
}

.cart-popup__product-card {
  display: grid;
  grid-template-columns: 1fr 20%;
}

.cart-popup__product-link {
  display: grid;
  width: 70%;
  grid-template-columns: 100px 100%;
  grid-template-rows: 1fr 1fr;
  justify-items: center;
  align-items: center;
  color: #1f2937;
  text-decoration: none;
  grid-column: 1/2;
}

.cart-popup__product-image {
  display: flex;
  width: 100%;
  height: 100%;
  max-width: 75px;
  max-height: 75px;
  padding-left: 10px;
  justify-self: left;
  grid-row: 1 / -1;
  grid-column: 1 / 2;
  border-radius: 0 10px;
  object-fit: cover;
}

.cart-popup__product-name {
  max-width: 600px;
  grid-column: 2 / -1;
  grid-row: 1 / 2;
  justify-self: left;
}

.cart-popup__product-price {
  display: flex;
  margin-left: auto;
  grid-column: 2 / -1;
  grid-row: 2 / -1;
}

.cart-popup__product-quantity {
  display: flex;
  margin-right: auto;
  grid-column: 2 / -1;
  grid-row: 2 / -1;
  justify-self: left;
}

.cart-popup__product-remove {
  position: relative;
  display: flex;
  box-sizing: border-box;
  margin: auto;
  margin-right: 20%;
  width: 15px;
  height: 15px;
  background-color: transparent;
  border: none;
  cursor: pointer;

  &::after,
  &::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 1px;
    background-color: $additional-grey;
  }

  &::after {
    transform: rotate3d(0, 0, 1, 45deg);
  }

  &::before {
    transform: rotate3d(0, 0, 1, -45deg);
  }
}

.cart-popup__subtotal {
  display: grid;
  width: 100%;
  padding: 0 20px;
  grid-template-columns: 1fr 1fr;
}

.cart-popup__subtotal-title {
  display: flex;
  margin-right: auto;
}

.cart-popup__subtotal-sum {
  display: flex;
  margin-left: auto;
}

.cart-popup__buttons {
  display: grid;
  padding: 10px;
  width: 100%;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}

.cart-popup__button {
  margin: auto;
  width: 100%;
  height: 100%;
  padding: 8px 16px;
  font-size: 0.75rem;

  &--checkout {
    border: none;
    background-color: darken($general-green, 20%);
  }
}
